// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

@mixin button-variant($background, $border, $hover-background: mix($background, #fff, 50%), $hover-border: $hover-background, $active-background: $background, $active-border: $background) {
    color: color-yiq($background);
    @include gradient-bg($background);
    border-color: $border;
    @include box-shadow($btn-box-shadow);

    @include hover-focus {
        // color: color-yiq($hover-background);
        color: color-yiq($background);
        @include gradient-bg($hover-background);
        border-color: $hover-border;
    }

    &:focus,
    &.focus {

        // Avoid using mixin so we can pass custom focus shadow properly
        @if $enable-shadows {
            box-shadow: $btn-box-shadow, 0 0 0 $btn-focus-width rgba(mix(color-yiq($background), $border, 15%), .5);
        }

        @else {
            // box-shadow: 0 0 0 $btn-focus-width rgba(mix(color-yiq($background), $border, 15%), .5);
            box-shadow: 0 0 4px 0 $hover-background;
        }
    }

    // Disabled comes first so active can properly restyle
    &.disabled,
    &:disabled {
        color: color-yiq($background);
        background-color: $background;
        border-color: $border;

        // Remove CSS gradients if they're enabled
        @if $enable-gradients {
            background-image: none;
        }
    }

    &:not(:disabled):not(.disabled):active,
    &:not(:disabled):not(.disabled).active,
    .show>&.dropdown-toggle {
        color: color-yiq($active-background);
        background-color: $active-background;

        @if $enable-gradients {
            background-image: none; // Remove the gradient for the pressed/active state
        }

        border-color: $active-border;
        box-shadow: 0 0 4px 0 $hover-background;

        &:focus {

            // Avoid using mixin so we can pass custom focus shadow properly
            @if $enable-shadows {
                box-shadow: $btn-active-box-shadow, 0 0 0 $btn-focus-width rgba(mix(color-yiq($background), $border, 15%), .5);
            }

            @else {
                // box-shadow: 0 0 0 $btn-focus-width rgba(mix(color-yiq($background), $border, 15%), .5);
                box-shadow: 0 0 4px 0 $hover-background;
            }
        }
    }
}

@mixin button-outline-variant($color, $color-hover: mix($color, #fff, 50%), $active-background: $color-hover, $active-border: $color-hover) {
    color: $color;
    border-color: $color;

    @include hover-focus {
        color: $color-hover;
        // background-color: $active-background;
        border-color: $active-border;
    }

    &:focus,
    &.focus {
        // box-shadow: 0 0 0 $btn-focus-width rgba($color, .5);
        box-shadow: 0 0 4px 0 $active-background;
        text-shadow: 0px 0px 20px $color-hover;
    }

    &.disabled,
    &:disabled {
        color: $color;
        background-color: transparent;
    }

    &:not(:disabled):not(.disabled):active,
    &:not(:disabled):not(.disabled).active,
    .show>&.dropdown-toggle {
        // color: color-yiq($active-background);
        color: $color;
        text-shadow: 0px 0px 20px $color-hover;
        // background-color: $active-background;
        border-color: $active-border;
        box-shadow: 0 0 4px 0 $active-background;

        &:focus {

            // Avoid using mixin so we can pass custom focus shadow properly
            @if $enable-shadows and $btn-active-box-shadow !=none {
                box-shadow: $btn-active-box-shadow, 0 0 0 $btn-focus-width rgba($color, .5);
                text-shadow: 0px 0px 20px $color-hover;
            }

            @else {
                // box-shadow: 0 0 0 $btn-focus-width rgba($color, .5);
                box-shadow: 0 0 4px 0 $active-background;
                text-shadow: 0px 0px 20px $color-hover;
            }
        }
    }
}

